import * as React from "react"
import {container, pageSelect, activePage} from "../styles/topBar.module.css"
import { Link } from "gatsby"

const TopBar = ({active}) => {
  return(
    <div className={container}>
      <Link to='/portfolio' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="gallery" ? activePage : undefined}`}>Gallery</div>
      </Link>
      <Link to='/portfolio-pages/programming' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="programming" ? activePage : undefined}`}>Programming</div>
      </Link>
      <Link to='/portfolio-pages/GIS' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="GIS" ? activePage : undefined}`}>GIS</div>
      </Link>
      <Link to='/portfolio-pages/live2D' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="live2D" ? activePage : undefined}`}>Live2D</div>
      </Link>
      <Link to='/portfolio-pages/UI-UX' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="UI/UX" ? activePage : undefined}`}>UI/UX</div>
      </Link>
      <Link to='/portfolio-pages/gamedev' style={{ textDecoration: 'none' }}>
        <div className={`${pageSelect} ${active==="gamedev" ? activePage : undefined}`}>Gamedev</div>
      </Link>
    </div>
  )
}

export default TopBar